import {tokenize, markEdits} from 'react-diff-view';
import refractor from 'refractor';

self.addEventListener(
    'message',
    ({data: {id, hunks, oldSource}}) => {
        const options = {
            highlight: true,
            refractor: refractor,
            language: 'jsx',
            enhancers: [
                markEdits(hunks)
            ]
        };

        if (oldSource) {
            Object.assign(options, {oldSource});
        }

        const tokens = tokenize(hunks, options);
        self.postMessage({id, tokens});
    }
);
